.stage{width:100vw;height:100vh;overflow:hidden;transform:translate3d(0, 0, 0)}.stage .my-nav-bar{position:fixed;top:25px;right:50px;z-index:1}.stage .my-story{transition:transform 1s ease-in-out}.stage .my-story.home{transform:translateY(-300vh)}.stage .my-story.sky{transform:translateY(-200vh)}.stage .my-story.atmosphere{transform:translateY(-100vh)}.stage .my-story.universe{transform:translateY(0)}.stage .my-rocket{position:absolute;left:200px;transition:top 1s ease-in-out}.stage .my-rocket.at-home{top:calc(400vh - 450px)}.stage .my-rocket.at-sky{top:calc(300vh - 600px)}.stage .my-rocket.at-atmosphere{top:calc(200vh - 700px)}.stage .my-rocket.at-universe{top:calc(100vh - 800px)}.stage .fork-me{width:150px;height:150px;background-image:url(data:image/png;base64,);background-size:contain;position:fixed;z-index:9;top:0;left:0;cursor:pointer;display:block}.stage .icp-beian{position:fixed;bottom:10px;left:50%;transform:translateX(-50%);text-align:center;color:white;font-size:16px}.stage .icp-beian a{color:inherit;text-decoration:none}.stage .icp-beian .beian::before{content:"";display:inline-block;margin-right:5px;width:20px;height:20px;background-image:url(data:image/png;base64,);background-size:contain;vertical-align:text-bottom}body{font-size:14px}

